.data-panel {
    max-height: 100%;
    position: relative;
    height: 100%;
    background: -webkit-gradient(linear, left top, right top, from(black), color-stop(transparent), to(transparent));
    background: -webkit-linear-gradient(left, black, transparent, transparent);
    background: -o-linear-gradient(left, black, transparent, transparent);
    background: linear-gradient(to right, black, transparent, transparent);
    margin-left: 80px;
}

@media screen and (max-width: 500px) {
    .data-panel {
        margin-left: 0px !important;
        padding-top:64px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
}



